<template>
  <div class="ranking" v-show="isShow">
    <div>
      <van-nav-bar title="排行" left-text left-arrow @click-left="onClickLeft" />
    </div>
    <div class="left-nav">
      <van-sidebar v-model="activeKey">
        <van-sidebar-item
          v-for="(listrankItem,listrankIndex) in listRankData"
          :key="listrankIndex"
          :title="listrankItem.name"
          @click="styleRank(listrankItem.id,listrankItem.description)"
        />
      </van-sidebar>
    </div>
    <div class="right-content">
      <div class="titleTop-text">{{contentPs}}</div>
      <div
        class="rank-item"
        v-for="(styleRankItem,styleRankIndex) in styleRankData"
        :key="styleRankIndex"
        @click="goIndeatail(styleRankItem.comic_id)"
      >
        <div class="img-box">
          <img v-lazy="styleRankItem.vertical_cover" />
        </div>
        <div class="title-box">
          <div class="title">{{styleRankItem.title}}</div>
          <div class="text-box">
            <div>{{styleRankItem.author[0]}}</div>
            <div v-for="(ietm,index) in styleRankItem.styles" :key="index">{{ietm.name}}</div>
            <div v-if="styleRankItem.is_finish !== 1">更新至{{styleRankItem.total}}话</div>
            <div v-else>[完结] 共{{styleRankItem.total}}话</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Ranking",
  data() {
    return {
      activeKey: 0,
      listRankData: [],

      styleRankData: [],
      StyleRankId: 7,
      contentPs: "",

      isShow: false
    };
  },
  created() {
    this.getListRank();
    this.getStyleRank();
  },
  methods: {
    // 前往漫画详情
    goIndeatail(id) {
      this.$router.push({
        name: "Indetail",
        params: {
          id
        }
      });
    },
    // 切换排行内容
    styleRank(id, value) {
      this.styleRankData = [];
      this.StyleRankId = id;
      this.getStyleRank();
    },
    // 返回上一级
    onClickLeft() {
      this.$router.back();
    },
    //  获取排行列表
    getListRank() {
      this.axios({
        method: "get",
        url: "/ListRank"
      })
        .then(res => {
          //  
          this.listRankData = res.data.data.list;
         //   
        })
        .catch(err => {
          
        });
    },
    //  获取排行漫画数据
    getStyleRank() {
      this.axios({
        method: "get",
        url: "/GetRankInfo",
        params: {
          id: this.StyleRankId,
          offset: 0,
          subId: 0
        }
      })
        .then(res => {
          this.styleRankData = res.data.data.list;
         //  
          this.contentPs =  res.data.data.description
         //   
          this.isShow = true;
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.ranking {
  .left-nav {
    position: fixed;
    left: 0;
    top: 46px;
    bottom: 0px;
    width: 80px;
    .van-sidebar-item--select::before{
      height: 0.8rem;
    }
  }
  .right-content {
    position: fixed;
    right: 0;
    top: 46px;
    bottom: 0;
    margin: auto;
    overflow-y: scroll;
    width: calc(100% - 80px);
    .titleTop-text{
      text-align: center;
      padding: 10px 0;
      color: #90959b;
    }

    .rank-item {
      display: flex;
      .img-box {
        width: calc(100% / 2);
        padding: 10px;
        img {
          width: 100%;
          display: block;
          border-radius: 5px;
        }
      }
      .title-box {
        width: calc(100% / 2);
        padding: 10px;
        padding-left: 0px;
        position: relative;
        .title {
          width: 100%;
          overflow: hidden;
          display: -webkit-box;
          text-overflow: ellipsis;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          font-size: 15px;
          position: absolute;
          top: 10px;
        }
        .text-box {
          margin-top: 80px;
          font-size: 13px;
          color: #90959b;
          position: absolute;
          bottom: 15px;
          & > div {
            margin: 5px 0;
            &:last-child {
              margin: 0px;
            }
          }
        }
      }
    }
  }
}
::v-deep .van-nav-bar .van-icon {
  color: #6b3800;
}
</style>